argin: 0 !important;\n  }\n  .c-card--mobile-fixed {\n    position: fixed;\n    top: $size_header_height;\n    left: 0;\n    width: 100%;\n    height: calc(100% - #{$size_header_height});\n    transform: translateX(100%);\n    transition: transform 0.3s;\n    overflow-y: auto;\n    z-index: 1;\n\n    &.is-open {\n      transform: translateX(0);\n    }\n\n    .c-card__header-actions {\n      margin-top: 0;\n      padding-top: 7px;\n    }\n\n    .c-card__body {\n      &--scrollable {\n        max-height: none;\n        overflow-y: hidden;\n      }\n    }\n  }\n\n  .c-card__header-title--mobile-fixed {\n    position: fixed;\n    bottom: 15px;\n    right: 15px;\n    background-color: $color_primary;\n    color: $color_white;\n    border-radius: 10px;\n    font-size: 12px;\n    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.10);\n    z-index: 2;\n  }\n}","@keyframes c-button-is-loading {\n  0%   { transform: translateX(-100%); }\n  100% { transform: translateX(200%); }\n}\n\n.c-button {\n  display: inline-block;\n  padding: 10px 20px;\n  margin: 2px 0;\n  cursor: pointer;\n  border-radius: 5px;\n  font-size: 14px;\n  background-color: $color_primary;\n  color: $color_white;\n  text-decoration: none;\n  border: none;\n  transition: all 0.15s;\n  outline: 0;\n  position: relative;\n\n  &[disabled] {\n    background-color: rgba($color_black, 0.65);\n\n    &:hover {\n      background-color: rgba($color_black, 0.65);\n      box-shadow: none;\n    }\n  }\n\n  &.is-loading {\n    color: rgba($color_white, 0.5);\n    overflow: hidden;\n\n    &:before {\n      content: '';\n      display: block;\n      width: 70%;\n      height: 2px;\n      background-color: rgba($color_white, 0.8);\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      animation: c-button-is-loading 1s infinite;\n    }\n  }\n\n  &:hover {\n    box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_primary,.25);\n    background-color: darken($color_primary, 5%);\n  }\n\n  &:active, &:focus {\n    background-color: darken($color_primary, 10%);\n  }\n\n  &--green {\n    background-color: $color_green;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_green,.25);\n      background-color: darken($color_green, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_green, 10%);\n    }\n  }\n\n  &--blue {\n    background-color: $color_blue;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_blue,.25);\n      background-color: darken($color_blue, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_blue, 10%);\n    }\n  }\n\n  &--yellow {\n    background-color: $color_yellow;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_yellow,.25);\n      background-color: darken($color_yellow, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_yellow, 10%);\n    }\n  }\n\n  &--orange {\n    background-color: $color_orange;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_orange,.25);\n      background-color: darken($color_orange, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_orange, 10%);\n    }\n  }\n\n  &--black {\n    background-color: $color_black;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_black,.25);\n      background-color: darken($color_black, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_black, 10%);\n    }\n  }\n  \n  &--cenere {\n    background-color: $color_cenere;\n    color: $color_black;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_cenere,.25);\n      background-color: darken($color_cenere, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_cenere, 10%);\n    }\n  }\n\n  &--transparent {\n    background-color: transparent;\n    color: $color_black;\n\n    &:hover {\n      background-color: transparent;\n      box-shadow: none;\n      text-decoration: underline;\n    }\n  \n    &:active, &:focus {\n      background-color: transparent;\n      box-shadow: none;\n      text-decoration: underline;\n    }\n  }\n\n  &--twitter {\n    background-color: $color_twitter;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_twitter,.25);\n      background-color: darken($color_twitter, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_twitter, 10%);\n    }\n  }\n\n  &--linkedin {\n    background-color: $color_linkedin;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_linkedin,.25);\n      background-color: darken($color_linkedin, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_linkedin, 10%);\n    }\n  }\n\n  &--toggl {\n    background-color: $color_toggl;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_toggl,.25);\n      background-color: darken($color_toggl, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_toggl, 10%);\n    }\n  }\n\n  &--dropbox {\n    background-color: $color_dropbox;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_dropbox,.25);\n      background-color: darken($color_dropbox, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_dropbox, 10%);\n    }\n  }\n\n  &--block {\n    display: block;\n    text-align: center;\n  }\n}\n\n@media (max-width: $breakpoint_sm) {\n\n  .c-button {\n    padding: 15px 20px;\n  }\n\n}","// Configuration\n// *********************************************************************\n\n$grid: (\n  columns: 24,\n  space: 7.5px,\n  breakpoints: (\n    xs: 581px,\n    sm: 769px,\n    md: 1025px,\n    lg: 1282px\n  )\n);\n\n// Style\n// *********************************************************************\n\n.c-grid__container, .c-grid__container-spaced {\n  margin: auto;\n  box-sizing: border-box;\n  @include clearfix;\n}\n\n.c-grid__col {\n  float: left;\n  box-sizing: border-box;\n  width: 100%;\n}\n\n// Basic grid\n// *********************************************************************\n\n@for $i from 1 through map-get($grid, 'columns') {\n  $width: percentage(1 / (map-get($grid, 'columns') / $i));\n\n  .c-grid__col--#{$i} {\n    width: $width;\n  }\n}\n\n@each $name, $pixels in map-get($grid, 'breakpoints') {\n  @media screen and (min-width: $pixels) {\n    @for $i from 1 through map-get($grid, 'columns') {\n      $width: percentage(1 / (map-get($grid, 'columns') / $i));\n    \n      .c-grid__col--#{$name}-#{$i} {\n        width: $width;\n      }\n    }\n  }\n}\n\n// Spaced grid\n// *********************************************************************\n\n.c-grid__container-spaced {\n  margin-left: - map-get($grid, 'space');\n  margin-right:  - map-get($grid, 'space');\n\n  > .c-grid__col {\n    margin-left: map-get($grid, 'space');\n    margin-right: map-get($grid, 'space');\n  }\n\n  @for $i from 1 through map-get($grid, 'columns') {\n    $width: percentage(1 / (map-get($grid, 'columns') / $i));\n  \n    > .c-grid__col--#{$i} {\n      width: calc(#{$width} - #{map-get($grid, 'space') * 2});\n    }\n  }\n  \n  @each $name, $pixels in map-get($grid, 'breakpoints') {\n    @media screen and (min-width: $pixels) {\n      @for $i from 1 through map-get($grid, 'columns') {\n        $width: percentage(1 / (map-get($grid, 'columns') / $i));\n      \n        > .c-grid__col--#{$name}-#{$i} {\n          width: calc(#{$width} - #{map-get($grid, 'space') * 2});\n        }\n      }\n    }\n  }\n}","// Form\n// *********************************************************************\n\n.c-form {\n  padding-bottom: 15px;\n  @include clearfix;\n}\n\n.c-form__actions {\n  margin-bottom: -15px;\n  text-align: right;\n}\n\n.c-form__title {\n  font-weight: bold;\n  font-size: 18px;\n  color: $color_black;\n  border-bottom: solid 1px $color_cenere;\n  padding-bottom: 5px;\n  margin-bottom: 15px;\n}\n\n// Input normal\n// *********************************************************************\n\n.c-input {\n  margin-bottom: 15px;\n}\n\n.c-input__label {\n  font-size: 14px;\n  color: $color_black;\n  display: block;\n  margin-bottom: 4px;\n  margin-left: 4px;\n}\n\n.c-input__label-subtitle {\n  font-size: 12px;\n  color: darken($color_cenere, 20%);\n  margin-bottom: 4px;\n  margin-left: 4px; \n  display: block;\n}\n\n.c-input__field {\n  display: block;\n  border: solid 1px darken($color_cenere, 10%);\n  border-radius: 5px;\n  background-color: $color_white;\n  color: $color_black;\n  padding: 10px;\n  outline: none;\n  width: 100%;\n  transition: all 0.15s;\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n\n  &:active, &:focus {\n    border-color: lighten($color_primary, 20%);\n    box-shadow: 0 2px 5px darken($color_cenere, 10%);\n  }\n\n  &[disabled] {\n    background-color: darken($color_white, 2%);\n\n    &:active, &:focus {\n      border-color: darken($color_cenere, 10%);\n      box-shadow: none;\n    }\n  }\n\n  &[required] {\n    // TODO\n  }\n\n  &.is-error {\n    border-color: $color_orange;\n  }\n}\n\ntextarea.c-input__field {\n  min-height: 100px;\n}\n\ninput[type=\"date\"].c-input__field {\n  padding: 7px 10px;\n  padding-top: 8px;\n}\n\n// Input action\n// *********************************************************************\n\n.c-input-action {\n\n}\n\n.c-input-action__field {\n  display: inline-block;\n  border: solid 1px darken($color_cenere, 5%);\n  border-right: none;\n  border-top-left-radius: 5px;\n  border-bottom-left-radius: 5px;\n  background-color: $color_white;\n  color: $color_black;\n  padding: 10px;\n  outline: none;\n  width: calc(100% - 60px);\n  transition: all 0.15s;\n\n  &:active, &:focus {\n    border-color: lighten($color_primary, 20%);\n    box-shadow: 0 2px 5px darken($color_cenere, 10%);\n  }\n\n  &.is-error {\n    border-color: $color_orange;\n  }\n}\n\n.c-input-action__button {\n  cursor: pointer;\n  display: inline-block;\n  border: solid 1px $color_primary;\n  border-top-right-radius: 5px;\n  border-bottom-right-radius: 5px;\n  background-color: $color_primary;\n  color: $color_white;\n  padding: 10px 0;\n  outline: none;\n  width: 60px;\n  float: right;\n  transition: all 0.15s;\n\n  &:hover {\n    background-color: darken($color_primary, 5%);\n  }\n\n  &[disabled] {\n    background-color: $color_black;\n    border-color: $color_black;\n\n    &:hover {\n      background-color: $color_black;\n    }\n  }\n}\n\n// Input checkbox\n// *********************************************************************\n\n.c-input-checkbox {\n  position: relative;\n  margin-bottom: 15px;\n}\n\n.c-input-checkbox__label {\n  display: inline-block;\n  font-size: 14px;\n  color: $color_black;\n  padding-left: 25px;\n  cursor: pointer;\n  position: relative;\n\n  &:before{\n    content: '';\n    display: inline-block;\n    height: 15px;\n    width: 15px; \n    border: solid 2px darken($color_cenere, 5%);\n    background-color: $color_white;\n    position: absolute; \n    left: 0;\n    top: 0;\n    border-radius: 5px;\n    transition: all 0.3s ease;\n  }\n\n  &:hover {\n    &:before{\n      background-color: darken($color_cenere, 5%);\n    }\n  }\n\n  &:after {\n    content: '';\n    display: inline-block;\n    height: 15px;\n    width: 15px;\n    background-color: $color_primary;\n    position: absolute;\n    left: 2px;\n    top: 2px;\n    border-radius: 4px;\n  }\n}\n\n.c-input-checkbox__field {\n  position: absolute;\n  top: 0; left: 0;\n  opacity: 0;\n}\n\n.c-input-checkbox .c-input-checkbox__field + .c-input-checkbox__label::after {\n  content: none;\n}\n\n.c-input-checkbox .c-input-checkbox__field:checked + .c-input-checkbox__label::after {\n  content: '';\n}\n\n.c-input-checkbox .c-input-checkbox__field[required] + .c-input-checkbox__label::before {\n  // Custom style for required checkbox\n}\n\n@media (max-width: $breakpoint_sm) {\n  .c-form__actions {\n    text-align: center;\n\n    .c-button {\n      display: block;\n      width: 100%;\n    }\n  }\n}\n\n// Input selector\n// *********************************************************************\n\n.c-input-selector {\n\n}\n\n.c-input-selector__head {\n  margin-bottom: 15px;\n}\n\n.c-input-selector__content {\n  border: solid 1px darken($color_cenere, 10%);\n  height: 200px;\n  overflow-y: auto;\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n// Dependency Choices\n// *********************************************************************\n\n.choices {\n  &.is-focused {\n    .choices__inner {\n      border-color: lighten($color_primary, 20%);\n      box-shadow: 0 2px 5px darken($color_cenere, 10%);\n    }\n  }\n\n  &.is-disabled {\n    .choices__inner {\n      background-color: darken($color_white, 2%);\n    }\n\n    .choices__input {\n      background-color: darken($color_white, 2%);\n      cursor: initial;\n    }\n  }\n\n  .choices__inner {\n    background-color: $color_white;\n    border-radius: 5px;\n    border-color: darken($color_cenere, 10%);\n    min-height: 40px;\n    padding-left: 10px;\n    padding-right: 10px;\n    padding-top: 7px;\n    padding-bottom: 2px;\n  }\n\n  .choices__input {\n    background-color: $color_white;\n  }\n\n  .choices__list--dropdown {\n    border-radius: 5px;\n    border-color: darken($color_cenere, 10%);\n    top: calc(100% + 2px);\n\n    &.is-active {\n      min-height: 150px;\n    }\n\n    .choices__list {\n      max-height: 150px;\n    }\n  }\n\n  .choices__list--multiple .choices__item {\n    background-color: $color_primary;\n    border-color: $color_primary;\n  }\n\n  .choices__list--multiple .choices__item.is-highlighted {\n      background-color: $color_primary;\n      border-color: $color_primary;\n  }\n}\n",".c-message {\n  padding: 30px 15px;\n}\n\n.c-message__title {\n  display: block;\n  text-align: center;\n  color: $color_black;\n  font-size: 30px;\n}\n\n.c-message__content {\n  display: block;\n  text-align: center;\n  color: $color_black;\n  font-size: 20px;\n  margin-top: 15px;\n\n  &--small {\n    max-width: 400px;\n    margin-left: auto;\n    margin-right: auto;\n  }\n}\n"]}]);
